	justify-content: space-around;
	align-items: center;
	margin-right: 30px;
}

.link {
	text-decoration: none;
	font-family: "Courier Prime", monospace;
	color: rgb(255, 255, 255);
}

.link:hover {
	cursor: pointer;
}

.small-link {
	font-size: 20px;
	font-weight: 200;
	transition: all 200ms ease;
}

.small-link:hover {
	font-size: 24px;
	transition: all 200ms ease;
