/* blinking cursor https://codepen.io/delgar/pen/rvRvZM*/
#blinking-cursor {
	background: rgb(51, 251, 12);
	line-height: 17px;
	margin-left: 10px;
	margin-bottom: 5px;
	animation: blink 1s infinite;
	width: 15px;
	height: 24px;
}

@keyframes blink {
	0% {
		background: rgba(255, 255, 255, 0);
	}

	35% {
		background: rgba(255, 255, 255, 0);
	}

	50% {
		background: lime;
	}

	85% {
		background: lime;
	}

	100% {
		background: rgba(255, 255, 255, 0);
	}
}

